<template>
    <div class="container">
        <v-back title="考勤结果详情" />
        <md-field>
            <div class="title flex space-between align_center">
                <div class="flex align_center" style="margin-right: 1vw;"><md-icon name="home" class="margin_right_space color-1"></md-icon> {{$route.query.collegeName}}【{{typeArr[$route.query.type]}}】{{$route.query.total}}{{$route.query.type == '2'?'条记录':'人'}}</div>
                <div class="flex align_center" style="width: 43vw;justify-content: flex-end;"><md-icon name="time" class="margin_right_space color-1"></md-icon> {{$route.query.time}}[{{$route.query.stage == 'am' ? '早' : '晚'}}]</div>
            </div>
            <md-field-item title="选择院系" :content="selectorValue3" @click="showSelector3" arrow solid />
        </md-field>
        <md-selector v-model="isSelectorShow3" :data="data3" @confirm="onSelectorConfirm3" min-height="320px" max-height="320px" title="选择院系" okText="确认" large-radius></md-selector>
        <div class="table">
            <div class="table_tr flex space-between border-bottom align_center" style="padding: 0 4vw;background-color: #fff;" v-if="$route.query.type == '2'">
                <div class="table_th" style="width: 35%;">姓名/班级</div>
                <div class="table_th" style="width: 25%;">通行楼栋</div>
                <div class="table_th" style="width: 20%;">状态</div>
                <div class="table_th" style="width: 20%;">时间</div>
            </div>
            <div class="table_body">
				<div style="flex-wrap: wrap;" class="flex space-between" v-if="$route.query.type != '2'">
				    <div v-for="(item, index) in list" :key="index" class="banner">
						<div @click="fullShow(item)">
							<span :class="'usertype_title usertype_'+item.usertype"></span>
							<div>姓名:&nbsp;&nbsp;<span class="username">{{item.username || '--'}}</span></div>
							<div style="flex-wrap: wrap;display: flex;">学号/工号:&nbsp;&nbsp;<span>{{item.usernbr}}</span></div>
							<div>班级:&nbsp;&nbsp;<span>{{item.college_name  || '--'}}{{item.org_names}}{{item.class_names}}</span></div>
							<div>住宿:&nbsp;&nbsp;<span>{{item.building_name}}{{item.dorm_nbr}}房{{item.bed_nbr}}号床</span></div>
							<div>通行时间:&nbsp;&nbsp;<span>{{item.entry_exit_time || (item.person_entry_exit ? item.person_entry_exit.entry_exit_time : '无记录')}}</span></div>
							<div>备注:&nbsp;&nbsp;<span>{{item.last_person_entry_exit ? (item.last_person_entry_exit.remark || '--') : '--'}}</span></div>
							<div style="margin-bottom: 1vw;border-bottom: 1px dashed;"></div>
							<div>最后通行时间:&nbsp;&nbsp;<span>{{item.last_person_entry_exit ?  item.last_person_entry_exit.entry_exit_time : '无记录'}}</span></div>
							<div>当前状态:&nbsp;&nbsp;<span :class="statusText(item) == '在寝' ? 'in_status' : statusText(item) == '未归' ? 'out_status' : 'undiefind_status'">{{statusText(item)}}</span><i class="iconfont icon-shezhi"></i></div>
							<i class="md-icon icon-font md-icon-setting setting lg"></i>
						</div>
				    </div>
				</div>
				
                <div class="table_tr flex space-between border-bottom align_center" style="background-color: #fff;margin: 0 -4vw;padding: 1vw 4vw;" v-for="(item,index) in list" :key="index" v-if="$route.query.type == '2'">
                    <div class="table_td" style="width: 35%;font-weight: bolder;font-size: 4vw;">{{item.student_name}}<div style="font-weight: normal;font-size: 3.5vw;">{{item.class_names}}</div></div>
                    <div class="table_td" style="width: 25%;color: #1acfc6;">{{item.b_building ? item.b_building.building_name : '--'}}</div>
                    <div class="table_td" :style="'width: 20%;font-weight: bold; ' + (item.entry_exit_type == 1 ? '' : 'color: #fdb731;')">{{item.entry_exit_type == 1 ? '进' : '出'}}</div>
                    <div class="table_td" style="width: 20%;color: #1acfc6;">{{item.entry_exit_time.split(' ')[1]}}</div>
                </div>
                <div v-show="list.length == 0" class="no_data">
                    <md-result-page text="没内容？切换院系试一试！"></md-result-page>
                </div>
            </div>
        </div>
        <md-landscape v-model="showFullScreen" :full-screen="false">
			<div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
			    <div style="margin-bottom: 4vw;">详细信息</div>
			    <div>
			        <img v-if="showData.photo" :src="showData.photo" alt="" style="display: block;margin: 0 auto 2vw;">
			        <img v-else src="@/assets/student.png" alt="" style="display: block;margin: 0 auto 2vw;">
			        <div style="color: #2f86f6;text-align: center;">{{detailStatusText(showData)}}</div>
			    </div>
			    
			</div>
			<div class="flex btns">
			    <div>
			        <div @click="statusChange(1)">在寝</div>
			    </div>
			    <div>
			        <div @click="statusChange(0)">未归</div>
			    </div>
			</div>
        </md-landscape>
    </div>
</template>

<script>
	var dayjs = require('dayjs')
    export default {
        name: 'report2_list',
        data() {
            return {
                typeArr: ['考勤人数','身份未登记人数','今日通行人次','24小时以上未归寝人员','24小时以上未出寝人员','闭寝时间未归人员','闭寝后晚归人员','闭寝后早出人员'],
                list: [],
                selectorValue3: '',
                value3: '',
                isSelectorShow3: false,
                data3: [],
                showFullScreen: false,
                showData: {

                }
            }
        },
        methods: {
			statusText(item) {
				if (item.last_person_entry_exit) {
					if (item.last_person_entry_exit.in_dorm_status == null) {
						return '无记录'
					} else {
						if (item.last_person_entry_exit.in_dorm_status == '0') {
							return '未归'
						} else {
							if (item.last_person_entry_exit.in_dorm_status == '1') {
								return '在寝'
							} else {
								return '未知'
							}
						}
					}
				} else {
					return '无记录'
				}
			},
			detailStatusText(showData) {
				let text = '未记录'
				let status = '--'
				if (showData.last_person_entry_exit) {
					if (showData.last_person_entry_exit.entry_exit_status == null) {
						text = '未记录'
					} else {
						if (showData.last_person_entry_exit.entry_exit_status == '0') {
							text = '正常'
						} else {
							text = '未记录'
						}
					}
					if (showData.last_person_entry_exit.in_dorm_status == null) {
						status = '--'
					} else {
						if (showData.last_person_entry_exit.in_dorm_status == '0') {
							status = '未归'
						} else {
							if (showData.last_person_entry_exit.in_dorm_status == '1') {
								status = '在寝'
							} else {
								status = '未知'
							}
						}
					}
				}
				return text + ' | ' + status;
			},
            showSelector3() {
                this.isSelectorShow3 = true
            },
            onSelectorConfirm3({text, value}) {
                this.value3 = value
                this.selectorValue3 = text
                this.getList()  
            },
            //获取学院
            getDepartment() {
                this.$getL('/college/department', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id
                }, (res)=>{
                    this.data3 = [{
                        value: 'all',
                        text: '全部'
                    }]
                    res.data.forEach((item) => {
                        this.data3.push({
                            value: item.orgId,
                            text: item.orgName
                        })
                    })
                    this.getList()
                })
            },
            getList() {
                this.$getL('/notice/list', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    type: this.$route.query.type,
                    buildingId: this.$route.query.buildingId,
                    college_id: this.value3,
                    time: this.$route.query.time,
					stage: this.$route.query.stage
                }, (res)=>{
                    console.log(res.data)
					let list = res.data
                    this.list = list
                    this.$toast.hide()
                })
            },
            fullShow(itm) {
                this.showData = itm
				this.showFullScreen = true
            },
            //改变状态
            statusChange(status) {
				this.$getL('/dorm/last_change', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id,
					username: JSON.parse(localStorage.getItem('user')).username,
				    studentNbr: this.showData.usernbr,
				    status: status
				}, (res)=>{
				    this.$toast.succeed('修改成功')
					let in_dorm_status = '2'
					if (this.showData.last_person_entry_exit) {
						in_dorm_status = this.showData.last_person_entry_exit.in_dorm_status
					}
					let remark = dayjs().format('YYYY-MM-DD HH:mm:ss') + ' ' + JSON.parse(localStorage.getItem('user')).username + ' 手动将人员在寝状态由' + (in_dorm_status == 0 ? '【未归】' : in_dorm_status == 1 ? '【在寝】' : '【未知】') + '修改为' + (status == 0 ? '【未归】' : '【在寝】')
					
					if (this.showData.last_person_entry_exit) {
						this.showData.last_person_entry_exit.in_dorm_status = status
						this.showData.last_person_entry_exit.remark = remark
						this.showData.last_person_entry_exit.entry_exit_status = 0
					} else {
						this.showData.last_person_entry_exit = {
							in_dorm_status: status,
							entry_exit_status: 0,
							remark: remark
						}
					}
				    setTimeout(() => {
						this.$toast.hide()
					}, 500)
				    
				})
            },
        },
        mounted() {
            this.getDepartment()
            this.selectorValue3 = '全部'
            this.value3 = 'all'
        }
    }
</script>

<style scoped>
	.md-field {
		padding: 2vw 4vw;
	}
    .title {
        border-bottom: 1px solid #e2e4ea;
        padding: 0 0 4vw;
        font-size: 3.5vw;
    }
    .margin_right_space {
        margin-right: 1vw;
    }
    .table {
        font-size: 3.5vw;
    }
    .table_body {
        height: calc(100vh - 49vw);
        overflow-y: auto;
		padding: 0 4vw 4vw;
    }
    .table_th {
        width: 20%;
        text-align: center;
        padding: 2vw 0;
    }
    .table_td {
        width: 20%;
        text-align: center;
        padding: 2vw 0;
        word-wrap: break-word;
    }
    .no_data {
        margin-top: 20vw;
    }
	.banner {
		background-color: #fff;
		width: calc(50% - 2vw);
		border-radius: 1vw;
		margin-bottom: 4vw;
		box-shadow: 5px 5px 10px rgba(0,0,0,.1);
		color: rgba(0,0,0,.5);
		position: relative;
		overflow: hidden;
	}
	.banner div{
	    padding: 1vw;
	}
	.banner .md-icon {
		position: absolute;
		width: 10vw;
		height: 10vw;
		bottom: 2vw;
		right: 2vw;
		font-size: 8vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.3);
		border-radius: 50%;
		text-align: center;
		line-height: 10vw;
	}
	.banner span {
		color: #2c3e50;
		font-weight: bolder;
	}
	.banner span.username {
		font-weight: bolder;
		font-size: 4.6vw;
	}
	.btns {
	    flex-wrap: wrap;
	}
	.btns > div {
	    width: 50%;
	    min-width: 50%;
	    margin-bottom: 4vw;
	}
	.btns > div div {
	    width: 60%;
	    margin: 0 auto;
	    border: 1px solid #ccc;
	    padding: 2vw;
	    border-radius: 2vw;
	}
	.banner_title {
		font-size: 3.5vw;
	}
	.usertype_title {
		display: block;
		border-radius: 1vw 1vw 0 0;
		padding: 1vw;
		margin: -1vw -1vw 0 -1vw;
		background-color: #666;
	}
	.usertype_S {
		background-color: #1acfc6;
	}
	.usertype_T {
		background-color: cornflowerblue;
	}
	.usertype_V {
		background-color: #ccc;
	}
	.usertype_Y {
		background-color: yellow;
	}
	.usertype_U {
		background-color: #666;
	}
</style>